.home__container {
    margin: 0;
    padding: 20px;

    .full__height {
        min-height: 95vh;
        position: relative;

        .search__menu {
            display: flex;
            align-items: center;

            .search {
                flex: 1;
                
                div {
                    width: 100%;
                }
            }

            .menu__dropdown {
                margin-left: 7px;
            }
        }

        .friends__list {
            max-height: 80vh;
            
            .list__item {
                padding-top: 20px !important;
                padding-bottom: 20px !important;
                cursor: pointer;
        
                &.active {
                    border-right: 5px solid #2185d0;
                    background-color: #2185d011;
                }
            }
        }
        
        &.center {
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }
}

.chat__box {
    height: 95vh;
    display: flex;
    flex-direction: column;
    padding-bottom: 10vh !important;

    .back__btn {
        cursor: pointer;
        display: none;
    }

    .info__btn {
        cursor: pointer;
        position: absolute;
        top: 10px;
        right: 10px;
    }

    .chat__input {
        position: absolute;
        bottom: 10px;
        left: 10px;
        right: 10px;
    }

    .chat__messages__container {
        margin-top: auto;
        max-height: 85vh;
        overflow-y: scroll;
        scroll-behavior: smooth;
        scrollbar-width: thin;
        box-shadow: 0 0 25px #fff inset;

        .chat__message {
            color: #fff;
            margin: 10px;
            
            span {
                padding: 5px 20px;
                background-color: #2185d0;
                border-radius: 10px;
                font-weight: bold;
            }
        }

        .our__message {
            text-align: right;
        }
    }
}


@media (max-width: 420px) {
    .full_height  {
        box-shadow: none !important;
    }

    .display__none__mobile {
        display: none !important;
    }

    .chat__box {
        .back__btn {
            display: block;
        }

        .chat__messages__container {
            margin-top: 10px;
        }
    }
}